.page-body {
  padding: 0;
  margin: 0 auto;
  width: 320px;
  outline: 1px solid @basic-black;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: normal;
  font-size: 14px;
  line-height: 18px;
  color: @basic-text;
}

@media (min-width: @tablet-width) {
  width: 768px;
  padding: 0 30px;
}

@media (min-width: @desktop-width) {
  width: 1440px;
  padding: 0 110px;
}
